export const cartItems = ({ cart }) => cart;
export const finalPrice = ({ cart }) => {
  const priceArr = [];
  cart.forEach(item => {
    if (item.cost_in_credits !== 'unknown') {
      priceArr.push(item.cost_in_credits);
    }
  });
  return priceArr.reduce((accumulator, currentValue) => accumulator + currentValue, 0);
};
export const unknownPricedItemsInCart = ({ cart }) => {
  const unknownPricedItems = cart.filter(item => item.cost_in_credits === 'unknown');
  return unknownPricedItems.length > 0;
};
