import Vue from 'vue';
import Router from 'vue-router';
import { routes as app } from './app';
const PageNotFound = () => import('./app/common/content/PageNotFound');

Vue.use(Router);

const routes = [...app, { path: '/404', name: '404', component: PageNotFound }, { path: '*', redirect: { name: '404' } }];

export default new Router({
  routes,
  linkActiveClass: 'active',
  mode: 'history',
  base: process.env.BASE_URL,
  scrollBehavior() {
    return { x: 0, y: 0 };
  },
});
