import { http } from '@/plugins/http';
import * as TYPES from './types';

export const fetchStarships = ({ dispatch, commit }, payload) => {
  dispatch('setFetching', { fetching: true });
  return http.get('/starships', { params: payload })
    .then(res => {
      const { count, next, previous, results } = res.data;
      const meta = { count, next, previous };
      dispatch('setStarships', results);
      dispatch('setStarshipsMeta', meta);
      commit(TYPES.INCREMENT_CURRENT_CHUNK);
    })
    .finally(() => dispatch('setFetching', { fetching: false }));
};

export const setStarships = ({ commit }, value) => {
  commit(TYPES.SET_STARSHIPS, value);
};

export const setStarshipsMeta = ({ commit }, value) => {
  commit(TYPES.SET_STARSHIPS_META, value);
};

export const updateStarshipValue = ({ commit }, value) => {
  commit(TYPES.UPDATE_STARSHIP_VALUE, value);
};

export const updateStarshipsValue = ({ commit }) => {
  commit(TYPES.UPDATE_STARSHIPS_VALUE);
};

export const clearStarships = ({ commit }) => {
  commit(TYPES.CLEAR_STARSHIPS);
};

export const clearStarshipsMeta = ({ commit }) => {
  commit(TYPES.CLEAR_STARSHIPS_META);
};

export const resetPage = ({ commit }) => {
  commit(TYPES.RESET_PAGE, {});
};

export const setSorting = ({ commit }, value) => {
  commit(TYPES.SET_SORTING, value);
};

export const clearSorting = ({ commit }) => {
  commit(TYPES.SET_SORTING, '');
  commit(TYPES.CLEAR_SORT_DIRECTION);
};

export const setSortDirection = ({ commit }, value) => {
  commit(TYPES.SET_SORT_DIRECTION, value);
};

export const clearSortDirection = ({ commit }) => {
  commit(TYPES.CLEAR_SORT_DIRECTION);
};

export const setSearchQuery = ({ commit }, value) => {
  commit(TYPES.SET_SEARCH_QUERY, value);
};

export const clearSearchQuery = ({ commit }) => {
  commit(TYPES.SET_SEARCH_QUERY, '');
};
