import * as TYPES from './types';

export default {
  [TYPES.ADD_TO_CART](state, value) {
    state.cart = [...state.cart, value];
  },
  [TYPES.REMOVE_FROM_CART](state, value) {
    const index = state.cart.findIndex(item => item.model === value.model);
    state.cart.splice(index, 1);
  },
  [TYPES.CLEAR_CART](state) {
    state.cart = [];
  },
};
