import * as TYPES from './types';
import { STARSHIPS_PER_PAGE } from '../constants';

export default {
  [TYPES.SET_STARSHIPS](state, value) {
    value.map(starship => {
      let cost = starship.cost_in_credits;
      if (cost !== 'unknown') {
        starship.cost_in_credits = Number(cost);
      }
      starship.inCart = false;
    });
    state.starships = [...state.starships, ...value];
  },
  [TYPES.CLEAR_STARSHIPS](state) {
    state.starships = [];
  },
  [TYPES.SET_STARSHIPS_META](state, value) {
    const { count } = value;
    const lastPage = Math.round(count / STARSHIPS_PER_PAGE);
    state.meta = { ...value, lastPage };
  },
  [TYPES.UPDATE_STARSHIP_VALUE](state, value) {
    const { starship, inCart } = value;
    const relevantStarship = state.starships.find(ship => ship.model === starship.model);
    relevantStarship.inCart = inCart;
  },
  [TYPES.UPDATE_STARSHIPS_VALUE](state) {
    state.starships.forEach(ship => ship.inCart = false);
  },
  [TYPES.CLEAR_STARSHIPS_META](state) {
    state.meta = {};
  },
  [TYPES.INCREMENT_CURRENT_CHUNK](state) {
    state.page += 1;
  },
  [TYPES.RESET_PAGE](state) {
    state.page = 1;
  },
  [TYPES.SET_SORTING](state, value) {
    state.sortParam = value;
  },
  [TYPES.SET_SORT_DIRECTION](state, value) {
    state.sortDirection = value;
  },
  [TYPES.CLEAR_SORT_DIRECTION](state) {
    state.sortDirection = 'asc';
  },
  [TYPES.SET_SEARCH_QUERY](state, value) {
    state.searchQuery = value;
  },
};
