import sortBy from 'lodash.sortby';

export const sortedStarships = ({ starships, sortParam, sortDirection, searchQuery }) => {
  const allStarships = sortBy(starships, sortParam)
    .filter(item => item.name.toLowerCase()
      .includes(searchQuery.trim().toLowerCase()));
  switch (sortDirection) {
    case 'asc':
      return allStarships;
    case 'desc':
      return allStarships.reverse();
    default:
      return allStarships;
  }
};
export const starshipsMeta = ({ meta }) => meta;
export const currentPage = ({ page }) => page;
