export default (http, store) => {

  http.interceptors.response.use(
    response => response,
    (error) => {
      const { response } = error;
      if ([500, 503].indexOf(response.status) > -1) {
        store.dispatch('setServerErrorModal', true);
      }
      store.dispatch('setFetching', {
        fetching: false
      });
      return Promise.reject(error);
    }
  );
};
